<script>
  export default {
    created () {
    },
    computed: {
      protocol: {
        get () {
          return this.$store.state.setting['mail.protocol']
        },
        set (value) {
          this.$store.commit('single', {
            key: 'mail.protocol',
            value: value
          })
        }
      },
      encryption: {
        get () {
          return this.$store.state.setting['mail.encryption']
        },
        set (value) {
          this.$store.commit('single', {
            key: 'mail.encryption',
            value: value
          })
        }
      },
      port: {
        get () {
          return this.$store.state.setting['mail.port']
        },
        set (value) {
          this.$store.commit('single', {
            key: 'mail.port',
            value: value
          })
        }
      },
      host: {
        get () {
          return this.$store.state.setting['mail.host']
        },
        set (value) {
          this.$store.commit('single', {
            key: 'mail.host',
            value: value
          })
        }
      },
      mail: {
        get () {
          return this.$store.state.setting['mail.mail']
        },
        set (value) {
          this.$store.commit('single', {
            key: 'mail.mail',
            value: value
          })
        }
      },
      username: {
        get () {
          return this.$store.state.setting['mail.username']
        },
        set (value) {
          this.$store.commit('single', {
            key: 'mail.username',
            value: value
          })
        }
      },
      password: {
        get () {
          return this.$store.state.setting['mail.password']
        },
        set (value) {
          this.$store.commit('single', {
            key: 'mail.password',
            value: value
          })
        }
      }
    },
    methods: {
      submit: function (e) {
        this.$validator.validateAll()
        if (this.errors.any()) {
          return false
        }
        this.$http.post(window.api + '/mail', {
          protocol: this.protocol,
          encryption: this.encryption,
          port: this.port,
          host: this.host,
          mail: this.mail,
          username: this.username,
          password: this.password
        }).then(response => {
          this.$store.commit('setting', response.body.data)
          this.$router.push('/email')
        }, response => {
          window.alert('')
        })
      }
    },
    mounted () {
      this.$store.commit('title', ' - Notadd Administration')
    }
  }
</script>
<style></style>
<template>
    <div class="box box-solid">
        <div class="box-header with-border">
            <h3 class="box-title"></h3>
        </div>
        <div class="box-body">
            <div class="form-horizontal">
                <div class="form-group" :class="{ 'has-error': errors.has('domain') }">
                    <label class="col-sm-1 control-label"></label>
                    <div class="col-sm-3">
                        <input name="protocol" type="text" class="form-control" placeholder="" v-model="protocol"
                               v-validate data-vv-rules="required">
                    </div>
                    <div class="col-sm-8">
                        <span class="help-block" v-show="errors.has('protocol')"></span>
                    </div>
                </div>
                <div class="form-group" :class="{ 'has-error': errors.has('domain') }">
                    <label class="col-sm-1 control-label"></label>
                    <div class="col-sm-3">
                        <input name="encryption" type="text" class="form-control" placeholder=""
                               v-model="encryption" v-validate data-vv-rules="required">
                    </div>
                    <div class="col-sm-8">
                        <span class="help-block" v-show="errors.has('encryption')"></span>
                    </div>
                </div>
                <div class="form-group" :class="{ 'has-error': errors.has('domain') }">
                    <label class="col-sm-1 control-label"></label>
                    <div class="col-sm-3">
                        <input name="host" type="text" class="form-control" placeholder="" v-model="host"
                               v-validate data-vv-rules="required">
                    </div>
                    <div class="col-sm-8">
                        <span class="help-block" v-show="errors.has('host')"></span>
                    </div>
                </div>
                <div class="form-group" :class="{ 'has-error': errors.has('domain') }">
                    <label class="col-sm-1 control-label"></label>
                    <div class="col-sm-3">
                        <input name="mail" type="text" class="form-control" placeholder="" v-model="mail"
                               v-validate data-vv-rules="required">
                    </div>
                    <div class="col-sm-8">
                        <span class="help-block" v-show="errors.has('mail')"></span>
                    </div>
                </div>
                <div class="form-group" :class="{ 'has-error': errors.has('domain') }">
                    <label class="col-sm-1 control-label"></label>
                    <div class="col-sm-3">
                        <input name="username" type="text" class="form-control" placeholder="" v-model="username"
                               v-validate data-vv-rules="required">
                    </div>
                    <div class="col-sm-8">
                        <span class="help-block" v-show="errors.has('username')"></span>
                    </div>
                </div>
                <div class="form-group" :class="{ 'has-error': errors.has('domain') }">
                    <label class="col-sm-1 control-label"></label>
                    <div class="col-sm-3">
                        <input name="password" type="password" class="form-control" placeholder=""
                               v-model="password" v-validate data-vv-rules="required">
                    </div>
                    <div class="col-sm-8">
                        <span class="help-block" v-show="errors.has('password')"></span>
                    </div>
                </div>
            </div>
        </div>
        <div class="box-footer">
            <button class="btn btn-primary" :disabled="errors.any()" @click="submit"></button>
        </div>
    </div>
</template>


