<script>
  export default {
    created () {
    },
    computed: {
      enabled: {
        get () {
          return this.$store.state.setting['site.enabled']
        },
        set (value) {
          this.$store.commit('single', {
            key: 'site.enabled',
            value: value
          })
        }
      },
      name: {
        get () {
          return this.$store.state.setting['site.name']
        },
        set (value) {
          this.$store.commit('single', {
            key: 'site.name',
            value: value
          })
        }
      },
      domain: {
        get () {
          return this.$store.state.setting['site.domain']
        },
        set (value) {
          this.$store.commit('single', {
            key: 'site.domain',
            value: value
          })
        }
      },
      beian: {
        get () {
          return this.$store.state.setting['site.beian']
        },
        set (value) {
          this.$store.commit('single', {
            key: 'site.beian',
            value: value
          })
        }
      },
      company: {
        get () {
          return this.$store.state.setting['site.company']
        },
        set (value) {
          this.$store.commit('single', {
            key: 'site.company',
            value: value
          })
        }
      },
      copyright: {
        get () {
          return this.$store.state.setting['site.copyright']
        },
        set (value) {
          this.$store.commit('single', {
            key: 'site.copyright',
            value: value
          })
        }
      },
      statistics: {
        get () {
          return this.$store.state.setting['site.statistics']
        },
        set (value) {
          this.$store.commit('single', {
            key: 'site.statistics',
            value: value
          })
        }
      }
    },
    methods: {
      submit: function (e) {
        this.$validator.validateAll()
        if (this.errors.any()) {
          return false
        }
        this.$http.post(window.api + '/setting/set', {
          enabled: this.enabled,
          name: this.name,
          domain: this.domain,
          beian: this.beian,
          company: this.company,
          copyright: this.copyright,
          statistics: this.statistics
        }).then(response => {
          this.$store.commit('setting', response.body.data)
          this.$router.push('/setting')
        }, response => {
          window.alert('')
        })
      }
    },
    mounted () {
      this.$store.commit('title', ' - Notadd Administration')
    }
  }
</script>
<template>
    <div class="box box-solid">
        <div class="box-header with-border">
            <h3 class="box-title"></h3>
        </div>
        <div class="box-body">
            <div class="form-horizontal">
                <div class="form-group">
                    <label class="col-sm-1 control-label"></label>
                    <div class="col-sm-3">
                        <div class="btn-group btn-switch">
                            <label class="btn btn-primary" :class="{ active: enabled === '1' }">
                                <input type="radio" v-model="enabled" value="1"> 
                            </label>
                            <label class="btn btn-primary" :class="{ active: enabled === '0' }">
                                <input type="radio" v-model="enabled" value="0"> 
                            </label>
                        </div>
                    </div>
                </div>
                <div class="form-group" :class="{ 'has-error': errors.has('name') }">
                    <label class="col-sm-1 control-label"></label>
                    <div class="col-sm-3">
                        <input name="name" type="text" class="form-control" placeholder="" v-model="name"
                               v-validate data-vv-rules="required">
                    </div>
                    <div class="col-sm-8">
                        <span class="help-block" v-show="errors.has('name')"></span>
                    </div>
                </div>
                <div class="form-group" :class="{ 'has-error': errors.has('domain') }">
                    <label class="col-sm-1 control-label"></label>
                    <div class="col-sm-3">
                        <input name="domain" type="text" class="form-control" placeholder="" v-model="domain"
                               v-validate data-vv-rules="required">
                    </div>
                    <div class="col-sm-8">
                        <span class="help-block" v-show="errors.has('domain')"></span>
                    </div>
                </div>
                <div class="form-group" :class="{ 'has-error': errors.has('beian') }">
                    <label class="col-sm-1 control-label"></label>
                    <div class="col-sm-3">
                        <input name="beian" type="text" class="form-control" placeholder="" v-model="beian"
                               v-validate data-vv-rules="required">
                    </div>
                    <div class="col-sm-8">
                        <span class="help-block" v-show="errors.has('beian')"></span>
                    </div>
                </div>
                <div class="form-group" :class="{ 'has-error': errors.has('company') }">
                    <label class="col-sm-1 control-label"></label>
                    <div class="col-sm-3">
                        <input name="company" type="text" class="form-control" placeholder="" v-model="company"
                               v-validate data-vv-rules="required">
                    </div>
                    <div class="col-sm-8">
                        <span class="help-block" v-show="errors.has('company')"></span>
                    </div>
                </div>
                <div class="form-group" :class="{ 'has-error': errors.has('copyright') }">
                    <label class="col-sm-1 control-label"></label>
                    <div class="col-sm-3">
                        <input name="copyright" type="text" class="form-control" placeholder=""
                               v-model="copyright" v-validate data-vv-rules="required">
                    </div>
                    <div class="col-sm-8">
                        <span class="help-block" v-show="errors.has('copyright')"></span>
                    </div>
                </div>
                <div class="form-group" :class="{ 'has-error': errors.has('statistics') }">
                    <label class="col-sm-1 control-label"></label>
                    <div class="col-sm-3">
                        <textarea name="statistics" class="form-control" rows="6" placeholder=""
                                  v-model="statistics" v-validate data-vv-rules="required"></textarea>
                    </div>
                    <div class="col-sm-8">
                        <span class="help-block" v-show="errors.has('statistics')"></span>
                    </div>
                </div>
            </div>
        </div>
        <div class="box-footer">
            <button class="btn btn-primary" :disabled="errors.any()" @click="submit"></button>
        </div>
    </div>
</template>


